  /* Table Styles */
  table {
    border-collapse: collapse;
    width: 850px;
    margin-left: 3%;
  }

  th, td {
    padding-left: 2px;
    padding-right: 2px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }

  th {
    background-color: #f2f2f2;
    color: #333;
    font-weight: bold;
  }

  tr:nth-child(even) {
    background-color: #f9f9f9;
  }
